/**
 * The following styles get applied inside the editor only.
 *
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-oese-tabs {
	border: 1px dotted #f00;
}
.oese-tab-row { display:block; width:100%; }
.oese-tab-row-hidden { display:none; }
#oeseTabs {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-left: 0;
    list-style-type: none;
    margin-bottom:0;
    border-bottom:1px solid #a0a0a0;
}
#oeseTabs li i.fas { font-family: "Font Awesome 5 Free" !important; }
#oeseTabs li { list-style:none; margin-left:1px; margin-right:1px; }
#oeseTabs li a.active {
    border-color: #dee2e6 #dee2e6 #fff;
    background-color: #92404d;
    color: #ffffff;
    text-decoration: none;
}
#oeseTabs li a { border-color: #dee2e6 #dee2e6 #fff; background-color:#414141; color:#ffffff; text-decoration:none; }
.oese-tabs-block div.tab-content .tab-pane { padding:10px 0; }